<!-- Home.vue -->
<template>
  <div>
    <h1>交易对信息</h1>
    <ul v-if="prices.length > 0">
      <li v-for="(item, index) in prices" :key="index" @click="goToDetail(index, item)">
        <div class="icon">
          <img :src="icons[index]" alt="icon" />
        </div>
        <strong>{{ symbols[index] }}</strong> - {{ parseFloat(item.lastPrice).toFixed(2) }} -
        {{ parseFloat(item.priceChangePercent).toFixed(2) }}%
      </li>
    </ul>
    <p v-else>Loading...</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      symbols: ["BTC", "ETH", "SOL", "BNB", "TON", "DOGE", "NEIRO", "APE", "SUI", "SHIB"], // 交易对数组
      icons: [
        require("@/assets/btc-icon-color/btc.png"),
        require("@/assets/btc-icon-color/eth.png"),
        require("@/assets/btc-icon-color/sol.png"),
        require("@/assets/btc-icon-color/bnb.png"),
        require("@/assets/btc-icon-color/ton.png"),
        require("@/assets/btc-icon-color/doge.png"),
        require("@/assets/btc-icon-color/neiro.png"),
        require("@/assets/btc-icon-color/ape.png"),
        require("@/assets/btc-icon-color/sui.png"),
        require("@/assets/btc-icon-color/shib.png"),
      ],
      prices: [], // 存储 API 返回的数据
    };
  },
  created() {
    this.fetchData(); // 在组件创建时获取数据
  },
  methods: {
    // 将 symbols 数组拼接成所需的字符串格式
    symbolsQuery() {
      // 使用 map() 方法给每个 symbol 添加 "USDT" 后缀，然后转换为 JSON 字符串
      return `symbols=${JSON.stringify(this.symbols.map((symbol) => symbol + "USDT"))}`;
    },

    async fetchData() {
      try {
        var url = "https://data-api.binance.vision/api/v3/ticker/24hr?" + this.symbolsQuery();
        console.log("url:", url);

        const response = await fetch(url);
        const data = await response.json(); // 解析 API 返回的 JSON 数据

        // 返回的数据是没有顺序的，要重新排
        this.symbols.forEach((symbol, i) => {
          data.forEach((item, j) => {
            if (item.symbol.startsWith(symbol)) {
              this.prices[i] = item;
            }
          });
        });
      } catch (error) {
        console.error("Error fetching data:", error); // 错误处理
      }
    },
    goToDetail(index, item) {
      // 使用 Vue Router 导航到新页面，并传递 `item` 数据
      this.$router.push({
        path: "/detail",
        query: {
          symbolName: this.symbols[index],
          symbol: item.symbol,
          // lastPrice: item.lastPrice,
          // priceChangePercent: item.priceChangePercent,
        },
      });
    },
  },
};
</script>

