h1 {
  color: #333;
}

ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: flex;
  align-items: center; /* 垂直居中对齐 */
  padding: 10px 0;
  font-size: 16px;
}

.icon {
  width: 60px;
  height: 60px;
  margin-right: 15px; /* 增加图片和文字的间距 */
}

.icon img {
  width: 100%;
  height: 100%;
  object-fit: cover; /* 确保图片填充容器 */
}

p {
  font-size: 14px;
  color: #999;
}
</style>
