<template>
  <div class="collect">
    <p class="title">自选</p>
    <div class="table">
      <div class="table-head">
        <div class="tr">
          <p class="td" v-for="(item, index) in tableTitle" :key="index">{{ item }}</p>
        </div>
      </div>
      <div class="table-body">
        <div class="list-item" v-for="(item, index) in priceData" :key="index" data-type="0">
          <div class="list-box" @touchstart.capture="touchStart" @touchend.capture="touchEnd">
            <div class="tr">
              <p class="td">{{ item.brand }}</p>
              <p class="td">{{ item.gold }}</p>
              <p class="td">{{ item.gold_bar }}</p>
              <p class="td">{{ item.unit }}</p>
            </div>
          </div>
          <div class="list-delete" @click="cancelStorageForItem(item)">移除自选</div>
        </div>
      </div>
    </div>
    <div class="btn" @click="showDialog = !showDialog">
      用户反馈
    </div>
    <div class="mask" v-show="showDialog" @click="showDialog = false"></div>
    <div class="dialog" v-show="showDialog">
      <input class="input" type="text" v-model="inputContent" placeholder="内容">
      <input class="input" type="text" v-model="inputPhone" placeholder="联系方式">
      <p class="submit" @click="submit">提交</p>
    </div>
  </div>
</template>
<script>
import axios from 'axios'
export default {
  name: 'collectCmp',
  // 定义变量
  data() {
    return {
      priceData: [],
      tableTitle: ['品牌', '黄金价格', '金条价格', '单位'],
      startX: 0,
      endX: 0,
      showDialog: false,
      inputContent: '',
      inputPhone: ''
    }
  },
  // 初始化数据
  created() {
    this.priceData = JSON.parse(localStorage.getItem('JLDCollect'))
  },
  methods: {
    // 滑动开始
    touchStart (e) {
      // 记录初始位置
      this.startX = e.touches[0].clientX
    },
    // 滑动结束
    touchEnd (e) {
      // 当前滑动的父级元素
      let parentElement = e.currentTarget.parentElement
      // 记录结束位置
      this.endX = e.changedTouches[0].clientX
      // 左滑
      if (parentElement.dataset.type == 0 && this.startX - this.endX > 30) {
        this.restSlide()
        parentElement.dataset.type = 1
      }
      // 右滑
      if (parentElement.dataset.type == 1 && this.startX - this.endX < -30) {
        this.restSlide()
        parentElement.dataset.type = 0
      }
      this.startX = 0
      this.endX = 0
    },
    // 判断当前是否有滑块处于滑动状态
    checkSlide () {
      let listItems = document.querySelectorAll('.list-item')
      for (let i = 0; i < listItems.length; i++) {
        if (listItems[i].dataset.type == 1) {
          return true
        }
      }
      return false
    },
    // 复位滑动状态
    restSlide () {
      let listItems = document.querySelectorAll('.list-item')
      // 复位
      for (let i = 0; i < listItems.length; i++) {
        listItems[i].dataset.type = 0
      }
    },
    // 移除自选
    cancelStorageForItem (item) {
      // 复位
      this.restSlide()
      this.priceData.forEach((data, index) => {
        if (data.brand === item.brand) {
          this.priceData.splice(index, 1)
        }
      })
      // 缓存数据
      localStorage.setItem('JLDCollect', JSON.stringify(this.priceData))
    },
    submit() {
      let params = {
        content: this.inputContent,
        phone: this.inputPhone,
        company: window.companyName
      }
      axios.post('https://jinjiacx.com/api/feedback', { params })
      .then((response) => {
        console.log(response)
        this.showDialog = false
      })
      .catch(function (error) {
        console.log(error);
      });
    }
  }
}
</script>

